<template><undefined>

<h1>Form 表单</h1>
<p>具有数据收集、校验和提交功能的表单，包含复选框、单选框、输入框、下拉选择框等元素。</p>
<h2 id="biao-dan"><a class="header-anchor" href="#biao-dan" aria-hidden="true">¶</a> 表单</h2>
<p>我们为 <code v-pre="">form</code> 提供了以下两种排列方式：</p>
<ul>
<li>水平排列：标签和表单控件水平排列；（默认）</li>
<li>垂直排列：标签和表单控件上下垂直排列；</li>
<li>行内排列：表单项水平行内排列。</li>
</ul>
<h2 id="biao-dan-yu"><a class="header-anchor" href="#biao-dan-yu" aria-hidden="true">¶</a> 表单域</h2>
<p>表单一定会包含表单域，表单域可以是输入控件，标准表单域，标签，下拉菜单，文本域等。<br>
这里我们封装了表单域 <code v-pre="">&lt;Form.Item /&gt;</code> 。</p>
<h2 id="zu-jian-yan-shi"><a class="header-anchor" href="#zu-jian-yan-shi" aria-hidden="true">¶</a> 组件演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;v-form&gt;\n    &lt;v-form-item label=\&quot;账户\&quot;&gt;\n        &lt;v-input placeholder=\&quot;请输入账户名\&quot;&gt;&lt;/v-input&gt;\n    &lt;/v-form-item&gt;\n    &lt;v-form-item label=\&quot;密码\&quot;&gt;\n        &lt;v-input placeholder=\&quot;请输入密码\&quot;&gt;&lt;/v-input&gt;\n    &lt;/v-form-item&gt;\n    &lt;v-form-item&gt;\n        &lt;v-checkbox&gt;记住我&lt;/v-checkbox&gt;\n    &lt;/v-form-item&gt;\n    &lt;v-form-item&gt;\n        &lt;v-button type=\&quot;primary\&quot; html-type=\&quot;submit\&quot;&gt;登录&lt;/v-button&gt;\n    &lt;/v-form-item&gt;\n&lt;/v-form&gt;\n&lt;br&gt;\n&lt;v-form&gt;\n    &lt;v-form-item&gt;\n        &lt;v-input placeholder=\&quot;用户名\&quot;&gt;\n            &lt;v-icon type=\&quot;user\&quot; slot=\&quot;before\&quot;&gt;&lt;/v-icon&gt;      \n        &lt;/v-input&gt;\n    &lt;/v-form-item&gt;\n    &lt;v-form-item&gt;\n        &lt;v-input placeholder=\&quot;密码\&quot; type=\&quot;password\&quot;&gt;\n            &lt;v-icon type=\&quot;lock\&quot; slot=\&quot;before\&quot;&gt;&lt;/v-icon&gt;      \n        &lt;/v-input&gt;\n    &lt;/v-form-item&gt;\n    &lt;v-form-item&gt;\n        &lt;v-button type=\&quot;primary\&quot; html-type=\&quot;submit\&quot;&gt;登录&lt;/v-button&gt;\n    &lt;/v-form-item&gt;\n&lt;/v-form&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><v-form>
    <v-form-item label="账户">
        <v-input placeholder="请输入账户名"></v-input>
    </v-form-item>
    <v-form-item label="密码">
        <v-input placeholder="请输入密码"></v-input>
    </v-form-item>
    <v-form-item>
        <v-checkbox>记住我</v-checkbox>
    </v-form-item>
    <v-form-item>
        <v-button type="primary" html-type="submit">登录</v-button>
    </v-form-item>
</v-form>
<br>
<v-form>
    <v-form-item>
        <v-input placeholder="用户名">
            <v-icon type="user" slot="before"></v-icon>      
        </v-input>
    </v-form-item>
    <v-form-item>
        <v-input placeholder="密码" type="password">
            <v-icon type="lock" slot="before"></v-icon>      
        </v-input>
    </v-form-item>
    <v-form-item>
        <v-button type="primary" html-type="submit">登录</v-button>
    </v-form-item>
</v-form>
</div>
