                <summary>
  #### 校验提示
  我们为表单控件定义了三种校验状态，为 定义 `validateStatus` 属性即可。 validateStatus: 'success', 'warning', 'error', 'validating'。 另外为输入框添加反馈图标，设置 `<FormItem>` 的 hasFeedback 属性值为 true 即可。 注意: 反馈图标只对 `<v-input>` 有效。
</v-input></FormItem></summary>
