<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="form-props"><a class="header-anchor" href="#form-props" aria-hidden="true">¶</a> Form Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>model</td>
<td>表单数据对象</td>
<td>object</td>
<td>-</td>
</tr>
<tr>
<td>rules</td>
<td>表单验证规则</td>
<td>object</td>
<td>-</td>
</tr>
<tr>
<td>direction</td>
<td>form 排列布局方式 inline、vertical或者horizontal</td>
<td>string</td>
<td>inline</td>
</tr>
<tr>
<td>showMessage</td>
<td>是否显示校验错误信息</td>
<td>boolean</td>
<td>true</td>
</tr>
</tbody>
</table>
<h3 id="form-methods"><a class="header-anchor" href="#form-methods" aria-hidden="true">¶</a> Form Methods</h3>
<table class="table"><thead>
<tr>
<th>方法名</th>
<th>说明</th>
<th>参数</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>validate</td>
<td>对整个表单进行校验的方法</td>
<td>callback(boolean)</td>
<td>-</td>
</tr>
<tr>
<td>validateField</td>
<td>validateField</td>
<td>prop: string,callback: function(errorMessage: string)</td>
<td>-</td>
</tr>
<tr>
<td>resetFields</td>
<td>对整个表单进行重置，将所有字段值重置为空并移除校验结果</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="form-item-props"><a class="header-anchor" href="#form-item-props" aria-hidden="true">¶</a> Form Item Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>prop</td>
<td>表单域 model 字段</td>
<td>传入 Form 组件的 model 中的字段</td>
<td>-</td>
</tr>
<tr>
<td>help</td>
<td>提示信息，如不设置，则会根据校验规则自动生成</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>hasFeedback</td>
<td>配合 validateStatus 属性使用，展示校验状态图标，建议只配合 Input 组件使用</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>validateStatus</td>
<td>校验状态，如不设置，则会根据校验规则自动生成('success' 'warning' 'error' 'validating')</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>required</td>
<td>是否必填，如不设置，则会根据校验规则自动生成</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>label</td>
<td>label 标签的文本</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>labelCol</td>
<td>label 标签布局，通 v-col 组件，设置 span offset 值，如 {span: 3, offset: 12}</td>
<td>object</td>
<td>-</td>
</tr>
<tr>
<td>wrapperCol</td>
<td>需要为输入控件设置布局样式时，使用该属性，用法同 labelCol</td>
<td>object</td>
<td>-</td>
</tr>
<tr>
<td>rules</td>
<td>表单验证规则</td>
<td>object</td>
<td>-</td>
</tr>
<tr>
<td>showMessage</td>
<td>是否显示校验错误信息</td>
<td>boolean</td>
<td>true</td>
</tr>
</tbody>
</table>
</undefined></template>
<style>
.box-demo-show .ant-form-horizontal{
    max-width: 540px;
}
</style>
<script>
export default {
    data() {
        var checkAge = (rule, value, callback) => {
            var age = parseInt(value, 10);

            setTimeout(() => {
                if (!Number.isInteger(age)) {
                    callback(new Error('请输入数字值'));
                } else {
                    if (age < 18) {
                        callback(new Error('必须年满18岁'));
                    } else {
                        callback();
                    }
                }
            }, 1000);
        };
        var validatePass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请输入密码'));
            } else {
                if (this.customForm.checkPass !== '') {
                    this.$refs.customRuleForm.validateField('checkPass');
                }
                callback();
            }
        };
        var validatePass2 = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请再次输入密码'));
            } else if (value !== this.customForm.pass) {
                callback(new Error('两次输入密码不一致!'));
            } else {
                callback();
            }
        };
        return {
            ruleForm: {
                name: '',
                region: '',
                date: '',
                delivery: false,
                type: [],
                resource: '',
                desc: ''
            },
            rules: {
                name: [{
                    required: true,
                    message: '请输入活动名称'
                }],
                region: [{
                    required: true,
                    message: '请选择活动区域'
                }],
                date: [{
                    required: true,
                    message: '请选择日期'
                }],
                type: [{
                    type: 'array',
                    required: true,
                    message: '请至少选择一个活动性质'
                }],
                resource: [{
                    required: true,
                    message: '请选择活动资源'
                }],
                desc: [{
                    required: true,
                    message: '请填写活动形式'
                }]
            },
            checkboxOpt: [{
                label: '美食/餐厅线上活动',
                value: '1'
            },
            {
                label: '地推活动',
                value: '2'
            },
            {
                label: '线下主题活动',
                value: '3'
            },
            {
                label: '单纯品牌曝光',
                value: '4'
            }],
            customForm: {
                pass: '',
                checkPass: '',
                age: ''
            },
            customRules: {
                pass: [{
                        required: true,
                        message: '请输入密码'
                    },
                    {
                        validator: validatePass
                    }
                ],
                checkPass: [{
                        required: true,
                        message: '请再次输入密码'
                    },
                    {
                        validator: validatePass2
                    }
                ],
                age: [{
                        required: true,
                        message: '请填写年龄'
                    },
                    {
                        validator: checkAge
                    }
                ]
            },
            dynamicValidateForm: {
                domains: [{
                    value: ''
                }],
                email: ''
            },
            labelCol: {
                span: 6
            },
            wrapperCol: {
                span: 14
            }
        } 
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    alert('submit!');
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        removeDomain(item) {
            var index = this.dynamicValidateForm.domains.indexOf(item)
            if (index !== -1) {
            this.dynamicValidateForm.domains.splice(index, 1)
            }
        },
        addDomain() {
            this.dynamicValidateForm.domains.push({
                value: '',
                key: Date.now()
            });
        }
    }
}
</script>


